'use client';
import { useState, useEffect } from 'react';
import { db } from '../../firebase/config';
import { collection, query, where, getDocs } from 'firebase/firestore';
import styles from './portfolioUpload.module.css';  // 改用 CSS Modules

export default function PortfolioUpload() {
  const [targetSchools, setTargetSchools] = useState([]);
  const [selectedFile, setSelectedFile] = useState(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedSchool, setSelectedSchool] = useState('');
  const [documentType, setDocumentType] = useState('learning');
  const [activeCategory, setActiveCategory] = useState('all');
  const [files, setFiles] = useState([
    // 這裡可以之後從 Firebase 獲取檔案列表
    {
      id: 1,
      name: '臺灣大學-資訊工程學系-學習歷程自述.pdf',
      size: '4.2 MB',
      uploadDate: '2025/03/18',
      status: 'analyzed',
      analysisDate: '2025/03/19',
      score: 92,
      school: 'ntu-csie',
      type: 'learning'
    },
    // ... 其他檔案
  ]);

  useEffect(() => {
    const fetchTargetSchools = async () => {
      try {
        const schoolsRef = collection(db, 'targetSchools');
        const q = query(schoolsRef, where('userId', '==', 'currentUserId')); // 替換成實際用戶 ID
        const querySnapshot = await getDocs(q);
        
        const schools = [];
        querySnapshot.forEach((doc) => {
          schools.push({ id: doc.id, ...doc.data() });
        });
        
        setTargetSchools(schools);
      } catch (error) {
        console.error('Error:', error);
      }
    };

    fetchTargetSchools();
  }, []);

  const handleFileUpload = (event) => {
    const file = event.target.files[0];
    setSelectedFile(file);
    setIsModalOpen(true);
  };

  const handleConfirmUpload = () => {
    if (!selectedSchool) {
      alert('請選擇志願學校');
      return;
    }

    if (!selectedFile) {
      document.getElementById('fileUpload').click();
      return;
    }

    // 這裡處理檔案上傳邏輯
    alert(`文件「${selectedFile.name}」上傳成功！`);
    setIsModalOpen(false);
    setSelectedFile(null);
    setSelectedSchool('');
  };

  const handleCategoryClick = (category) => {
    setActiveCategory(category);
  };

  const handleAnalyzeClick = () => {
    const selectedFileInput = document.querySelector('input[name="fileSelect"]:checked');
    if (selectedFileInput) {
      const fileItem = selectedFileInput.closest('.file-item');
      const fileName = fileItem.querySelector('.file-name').textContent;
      const fileStatus = fileItem.querySelector('.file-status');

      localStorage.setItem('currentAnalysis', JSON.stringify({
        fileName: fileName,
        timestamp: new Date().getTime()
      }));

      if (fileStatus.classList.contains('status-analyzed')) {
        if (confirm('此文件已經分析過，確定要重新分析嗎？')) {
          window.location.href = '/initAnalysis';
        }
      } else {
        window.location.href = '/initAnalysis';
      }
    } else {
      alert('請先選擇一個要分析的文件');
    }
  };

  return (
    <div className={styles.portfolioPage}>
      <div className={styles['main-content']}>
        <div className={styles['content-container']}>
          <div className={styles['page-header']}>
            <h1 className={styles['page-title']}>學習歷程</h1>
          </div>
          
          <div className={styles['portfolio-container']}>
            {/* 左側類別選擇 */}
            <div className={styles['portfolio-sidebar']}>
              <h3 className={styles['category-title']}>志願學校系所</h3>
              <ul className={styles['category-list']}>
                <li 
                  className={`${styles['category-item']} ${activeCategory === 'all' ? styles['active'] : ''}`}
                  onClick={() => handleCategoryClick('all')}
                >
                  <i className="fas fa-list"></i> 所有文件
                </li>
                {targetSchools.map((school) => (
                  <li 
                    key={school.id} 
                    className={`${styles['category-item']} ${activeCategory === school.code ? styles['active'] : ''}`}
                    onClick={() => handleCategoryClick(school.code)}
                  >
                    <i className="fas fa-university"></i> {school.name}
                  </li>
                ))}
              </ul>
            </div>
            
            {/* 右側內容 */}
            <div className={styles['portfolio-content']}>
              {/* 上傳區域 */}
              <div className={styles['upload-section']} id={styles['uploadArea']}>
                <div className={styles['upload-icon']}>
                  <i className="fas fa-cloud-upload-alt"></i>
                </div>
                <h3 className={styles['upload-title']}>上傳你的學習歷程文件</h3>
                <p className={styles['upload-subtitle']}>支持 PDF、Word、PPT、圖片等格式，最大 20MB</p>
                <button className={styles['upload-button']} onClick={() => document.getElementById('fileUpload').click()}>
                  <i className="fas fa-plus"></i> 選擇文件
                </button>
                <input 
                  type="file" 
                  id="fileUpload" 
                  style={{ display: 'none' }} 
                  onChange={handleFileUpload}
                  multiple 
                />
              </div>

              {/* 文件列表 */}
              <div className={styles['files-section']}>
                <div className={styles['section-header']}>
                  <h3 className={styles['section-title']}>
                    {activeCategory === 'all' ? '所有文件' : 
                      targetSchools.find(s => s.code === activeCategory)?.name + ' 申請資料'}
                  </h3>
                </div>
                
                <ul className={styles['files-list']}>
                  {files
                    .filter(file => activeCategory === 'all' || file.school === activeCategory)
                    .map((file, index) => (
                      <li key={file.id} className={styles['file-item']} data-school={file.school} data-type={file.type}>
                        <div className={styles['checkbox-container']}>
                          <input type="radio" name="fileSelect" id={`file${file.id}`} />
                        </div>
                        <div className={styles['file-icon']}>
                          <i className="fas fa-file-pdf"></i>
                        </div>
                        <div className={styles['file-details']}>
                          <div className={styles['file-name']}>{file.name}</div>
                          <div className={styles['file-meta']}>{file.size} · 上傳於 {file.uploadDate}</div>
                        </div>
                        <span className={`${styles['file-status']} status-${file.status}`}>
                          {file.status === 'analyzed' ? 
                            `已分析 · ${file.analysisDate} · 評分: ${file.score}` : 
                            '待分析'}
                        </span>
                        <button className={styles['delete-btn']}>
                          <i className="fas fa-trash"></i>
                        </button>
                      </li>
                    ))}
                </ul>
                
                <div className={styles['analyze-footer']}>
                  <button className={styles['analysis-button']} onClick={handleAnalyzeClick}>
                    <i className="fas fa-robot"></i> AI 分析所選文件
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* 上傳配置模態框 */}
      {isModalOpen && (
        <div className={styles['modal']}>
          <div className={styles['modal-content']} style={{ maxWidth: '500px' }}>
            <div className={styles['modal-header']}>
              <h3 className={styles['modal-title']}>設定文件屬性</h3>
              <button className={styles['close-btn']} onClick={() => setIsModalOpen(false)}>
                <i className="fas fa-times"></i>
              </button>
            </div>
            <div className={styles['modal-body']}>
              <div className={styles['upload-form']}>
                <div className={styles['form-group']}>
                  <label htmlFor="schoolSelect">選擇志願學校系所：</label>
                  <select 
                    id="schoolSelect" 
                    className={styles['form-control']}
                    value={selectedSchool}
                    onChange={(e) => setSelectedSchool(e.target.value)}
                  >
                    <option value="">請選擇學校系所...</option>
                    {targetSchools.map(school => (
                      <option key={school.id} value={school.code}>
                        {school.name}
                      </option>
                    ))}
                  </select>
                </div>
                
                <div className={styles['form-group']}>
                  <label>選擇文件類型：</label>
                  <div className={styles['radio-group']}>
                    <label className={styles['radio-label']}>
                      <input 
                        type="radio" 
                        name="documentType" 
                        value="learning"
                        checked={documentType === 'learning'}
                        onChange={(e) => setDocumentType(e.target.value)}
                      />
                      <span>學習歷程自述</span>
                    </label>
                    <label className={styles['radio-label']}>
                      <input 
                        type="radio" 
                        name="documentType" 
                        value="diversity"
                        checked={documentType === 'diversity'}
                        onChange={(e) => setDocumentType(e.target.value)}
                      />
                      <span>多元表現</span>
                    </label>
                  </div>
                </div>
                
                <div className={styles['form-group']}>
                  <label>選擇的文件：</label>
                  <div className={styles['selected-file-name']}>
                    {selectedFile ? selectedFile.name : '尚未選擇文件'}
                  </div>
                </div>
                
                <div className={styles['form-group']}>
                  <label>最終檔案名稱：</label>
                  <div className={styles['final-file-name']}>
                    {selectedFile && selectedSchool ? 
                      `${targetSchools.find(s => s.code === selectedSchool)?.name}-${
                        documentType === 'learning' ? '學習歷程自述' : '多元表現'
                      }.${selectedFile.name.split('.').pop()}` : 
                      '-'}
                  </div>
                </div>
              </div>
            </div>
            <div className={styles['modal-footer']}>
              <button className={styles['modal-btn']}>
                取消
              </button>
              <button className={styles['modal-btn']}>
                確認上傳
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
